import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Loader from '../../Components/Loader';
import Section from '../../Components/Section';
import Message from '../../Components/Message';
import Poster from '../../Components/Poster';
import Helmet from 'react-helmet';

const Container = styled.div`padding: 30px 20px;`;

const Form = styled.form`
	padding-bottom: 50px;
	width: 100%;
`;

const Input = styled.input`
	all: unset;
	font-size: 32px;
	font-weight: 440;
	width: 100%;
`;

const SearchPresenter = ({ movieResults, tvResults, searchTerm, error, loading, handleSubmit, updateTerm }) => (
	<React.Fragment>
		<Helmet>
			<title>Search | Movie Curator</title>
		</Helmet>
		<Container>
			<Form onSubmit={handleSubmit}>
				<Input placeholder="Search Movies or TV Shows..." value={searchTerm} onChange={updateTerm} />
			</Form>
			{loading ? (
				<Loader />
			) : (
				<React.Fragment>
					{movieResults &&
					movieResults.length > 0 && (
						<Section title="Movie Results">
							{movieResults.map((movie) => (
								<Poster
									key={movie.id}
									id={movie.id}
									title={movie.original_title}
									imageUrl={movie.poster_path}
									rating={movie.vote_average}
									year={movie.release_date && movie.release_date.substring(0, 4)}
									isMovie={true}
								/>
							))}
						</Section>
					)}
					{tvResults &&
					tvResults.length > 0 && (
						<Section title="TV Show Results">
							{tvResults.map((show) => (
								<Poster
									key={show.id}
									id={show.id}
									title={show.original_name}
									imageUrl={show.poster_path}
									rating={show.vote_average}
									year={show.first_air_date && show.first_air_date.substring(0, 4)}
								/>
							))}
						</Section>
					)}
					{error && <Message color="#fa983a" text={error} />}
					{movieResults &&
					tvResults &&
					movieResults.length === 0 &&
					tvResults.length === 0 && <Message color="#7f8c8d" text="Nothing Found" />}
				</React.Fragment>
			)}
		</Container>
	</React.Fragment>
);

SearchPresenter.propTypes = {
	movieResults: PropTypes.array,
	tvResults: PropTypes.array,
	searchTerm: PropTypes.string,
	error: PropTypes.string,
	loading: PropTypes.bool.isRequired,
	handleSubmit: PropTypes.func.isRequired,
	updateTerm: PropTypes.func.isRequired
};

export default SearchPresenter;
